@import "./assets/scss/utils/libs";
@import "./assets/scss/utils/helpers/vars";
@import "./assets/scss/utils/helpers/mixins";

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.canvasContentContainer {
  display: flex;
  align-items: flex-start;
  max-width: 800px;
}

.canvasObjectsList {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  flex-grow: 1;
}

